import React, { useEffect, useState } from 'react'
import { API_KEY, API_URL } from '../../API'

function StarRating(props) {
    const AVR_RATE = Math.round(props.voteAverage * 10) ; // 
    const STAR_IDX_ARR = ['first', 'second', 'third', 'fourth', 'last']; //  
    const [ratesResArr, setRatesResArr] = useState([0,0,0,0,0]); //  
    const calcStarRates = () => {
        let tempStarRatesArr = [0,0,0,0,0]; //
        let starVerScore = (AVR_RATE * 70) / 100;// width 14 14*5=70, 100    
        let idx = 0;
        while (starVerScore > 14) {//starVerScore 14  ,   tempStarRatesArr[idx] = starVerScore;  
            tempStarRatesArr[idx] = 14;
            idx += 1; //  0  
            starVerScore -= 14;
        }
        tempStarRatesArr[idx] = starVerScore;
        return tempStarRatesArr;    //  80 [14, 14, 14, 14, 0]
    };
    useEffect(() => {
        setRatesResArr(calcStarRates) //     
    }, [])
    console.log(ratesResArr)
    return (
        <div className='star_rate'>
            {STAR_IDX_ARR.map((item, idx) => {
                return (
                    <span className="star_icon" key={idx}>
                        <svg xmlns='http://www.w3.org/2000/svg' width='30' height='29' viewBox='0 0 14 13' fill='rgba(255,255,255,0.5)'>
                            {/* clip-path       . */}
                            <clipPath id={`${item}StarClip`}>
                                {/*  width svg viewBox . width 14     7   . */}
                                <rect width={`${ratesResArr[idx]}`} height='29' />
                            </clipPath>
                            <path
                                id={`${item}Star`}
                                d='M9,2l2.163,4.279L16,6.969,12.5,10.3l.826,4.7L9,12.779,4.674,15,5.5,10.3,2,6.969l4.837-.69Z'
                                transform='translate(-2 -2)'
                            />
                            {/* clip-path   .     ,  id    .  */}
                            <use clipPath={`url(#${item}StarClip)`} href={`#${item}Star`} fill='#e22f14'/>
                        </svg>
                    </span>
                )
            })}
        </div>
    )
}

export default StarRating